body {
  background: #e8e6f5;
  margin: 0;
  padding: 0;
}

button {
  height: 40px;
  background: #e8e6f5;
  border: 1px solid #4843b5;
  border-radius: 5px;
  margin: 0;
  padding: 0 5px;
}

.header {
  width: 100%;
  height: 50px;
  background: #4843b5;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form {
  justify-content: center;
  align-items: center;
  display: flex;
}

.form__input {
  height: 40px;
  width: 250px;
  background: #e8e6f5;
  border: 1px solid #4843b5;
  border-radius: 5px;
  padding-left: 25px;
  position: relative;
}

.form__input::-ms-input-placeholder {
  padding-left: 5px;
}

.form__input::placeholder {
  padding-left: 5px;
}

.form__btn {
  transition-property: scale(1);
  margin-left: 10px;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.form__btn:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.form__svg {
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 50px auto 30px;
  padding: 0;
  list-style: none;
  display: grid;
}

img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

.load-more {
  cursor: pointer;
  width: 150px;
  height: 50px;
  color: #e8e6f5;
  background-color: #4843b5;
  border: 0;
  border-radius: 11px;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  display: block;
}

.load-more:hover {
  color: #4843b5;
  background-color: #fff;
  border: 1px solid #4843b5;
}

